import Image from "next/image";
import Date from '@/components/common/DateComponent';
import { ArticleModel } from '@/model/props';
import Link from "next/link";
import useSWR from 'swr';

const fetcher = (url: string) => fetch(url).then((res) => res.json());

interface PostType {
    loadingItems?: Number
}

function Loading({ loadingItems }: { loadingItems?: Number }) {
    const items = [...Array(loadingItems)];
    return (
        <div className='grid grid-cols-3 gap-6 lg:mt-20 mt-10'>
            {items.map((_, i) => {
                return (
                    i % 2 == 0 ? (
                        <article key={i} className="flex flex-col">
                            <div className='animate-pulse h-2 lg:h-4 md:h-3 bg-secondary mb-3 lg:mb-6 w-1/3 rounded-lg'></div>
                            <div className='animate-pulse basis-[19%] relative w-full grow aspect-square md:h-48 bg-secondary mb-3 lg:mb-6 rounded-lg'></div>
                            <div className="animate-pulse h-3 lg:h-5 md:h-4 bg-secondary mb-3 lg:mb-6 w-1/2 rounded-lg"></div>
                            <div className="animate-pulse h-2 lg:h-4 md:h-3 bg-secondary mb-2 rounded-lg"></div>
                            <div className="animate-pulse h-2 lg:h-4 md:h-3 bg-secondary rounded-lg"></div>
                        </article>
                    ) : (
                        <article key={i} className="flex flex-col">
                            <div className='animate-pulse h-2 lg:h-4 md:h-3 bg-secondary mb-3 lg:mb-6 w-1/3 rounded-lg'></div>
                            <div className="animate-pulse h-3 lg:h-5 md:h-4 bg-secondary mb-3 lg:mb-6 w-1/2 rounded-lg"></div>
                            <div className="animate-pulse h-2 lg:h-4 md:h-3 bg-secondary mb-2 rounded-lg"></div>
                            <div className="animate-pulse h-2 lg:h-4 md:h-3 bg-secondary mb-3 lg:mb-6 rounded-lg"></div>
                            <div className='animate-pulse basis-[19%] relative w-full grow aspect-square md:h-48 bg-secondary rounded-lg'></div>
                        </article>
                    )
                )
            })}
        </div>
    )
}

export default function HighLightArticleComponent({ props }: { props: PostType }) {
    const {data, isLoading, error} = useSWR<ArticleModel[]>(`${process.env.BASE_URL + 'api/articles/highlight'}`, fetcher);

    if (isLoading) return <Loading loadingItems={props.loadingItems ? props.loadingItems : 3} />
    if (error) return (<></>)
    if (!data) return (<></>)

    return (
        <div className='grid grid-cols-3 gap-6 lg:mt-20 mt-10'>
            { data && data.map((item, i) => {
                return (
                    i % 2 == 0 ? (
                        <article key={i}>
                            <Link href={`/articles/${encodeURIComponent(item.slug)}`} className="flex flex-col">
                                <Date props={{ date: item.published_at, class: "text-partial font-dm-sans font-bold text-base mb-3 lg:mb-6" }} />
                                <div className="mb-3 lg:mb-6 basis-[19%] relative w-full grow aspect-square">
                                    {
                                        item.thumbnail_url ?
                                            <Image src={item.thumbnail_url} fill alt={item.title} /> :
                                            <div className="animate-pulse h-2 lg:h-4 md:h-3 bg-secondary rounded-lg"></div>
                                    }
                                </div>
                                <h1 className="text-partial font-dm-sans font-bold text-xl mb-3 lg:mb-6 truncate">{item.title}</h1>
                                <p dangerouslySetInnerHTML={{ __html: item.content }} className="text-partial font-dm-sans text-base leading-6 line-clamp-3"></p>
                            </Link>
                        </article>
                    ) : (
                        <article key={i}>
                            <Link href={`/articles/${encodeURIComponent(item.slug)}`} className="flex flex-col">
                                <Date props={{ date: item.published_at, class: "text-partial font-dm-sans font-bold text-base mb-3 lg:mb-6" }} />
                                <h1 className="text-partial font-dm-sans font-bold text-xl mb-3 lg:mb-6 truncate">{item.title}</h1>
                                <p dangerouslySetInnerHTML={{ __html: item.content }} className="text-partial font-dm-sans text-base leading-6 line-clamp-3 mb-3 lg:mb-6"></p>
                                <div className="basis-[19%] relative w-full grow aspect-square">
                                    {
                                        item.thumbnail_url ?
                                            <Image src={item.thumbnail_url} fill alt={item.title} /> :
                                            <div className="animate-pulse h-2 lg:h-4 md:h-3 bg-secondary rounded-lg"></div>
                                    }
                                </div>
                            </Link>
                        </article>
                    )
                )
            })}
        </div>
    )
}